.header {
  background: $header-gradient;
  padding: 0;
  margin: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;

  @include vp-1023 {
    position: absolute;
  }

  @include vp-767 {
    padding: 0 25px;
  }
}

.header__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  @include vp-1023 {
    display: grid;
    grid-template-columns: 215px 1fr;
    align-items: start;
  }

  @include vp-767 {
    grid-template-columns: 35px 1fr;
  }
}

.header__wrapper a {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 30px;
  margin-bottom: 35px;

  @include vp-1023 {
    margin: 0;
    padding-top: 20px;
    padding-bottom: 25px;
  }

  @include vp-767 {
    padding-top: 15px;
    padding-bottom: 20px;
  }

  @include hover-focus {
    opacity: 0.8;
  }
}

.header__svg-logo {
  width: 215px;
  height: 55px;

  @include vp-767 {
    display: none;
  }
}

.header__svg-logo--mobile {
  display: none;

  @include vp-767 {
    display: block;
    width: 35px;
    height: 35px;
  }
}

.header__contacts {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 480px;
  margin-left: 15px;
  margin-top: 40px;
  margin-bottom: 50px;

  @include vp-1023 {
    gap: 20px;
    width: 100%;
    margin-left: 0;
    padding-right: 20px;
    margin-top: 20px;
    margin-bottom: 40px;
    justify-content: end;
  }

  @include vp-767 {
    padding-right: 10px;
    margin-top: 10px;
    margin-bottom: 20px;
  }
}

.header__contacts a {
  margin-top: 5px;
  margin-bottom: 5px;

  @include vp-1023 {
    padding: 10px;
    margin-top: 0;
    margin-bottom: 0;
  }

  @include vp-767 {
    padding: 10px;
  }

  @include hover-focus {
    opacity: 0.8;
  }
}

.header__contacts span {
  font-family: $ff-pt-mono;
  font-size: $font-size-default;
  line-height: $line-height-mini;
  color: $color-font-white;
  min-width: 205px;
  padding-left: 15px;

  @include vp-1023 {
    display: none;
  }
}

.header__button {
  background-color: $color-black-pearl;
  color: $color-font-white;
  font-family: $ff-roboto-condensed;
  font-size: $font-size-small;
  line-height: $line-height-small;
  border: 1px solid $color-font-white;
  border-radius: 3px;
  padding: 10px 15px;
  margin-top: 35px;
  margin-bottom: 45px;
  margin-right: 63px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  flex: 1;
  max-width: 160px;

  @include vp-1023 {
    display: none;
  }
}
