@font-face {
  font-family: "PT Mono";
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

body.locked {
  overflow: hidden;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1242px;
  margin: 0 auto;
  padding: 0 10px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 49px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0;
  }
}

@media (max-width: 1023px) {
  .container--advantages {
    padding-left: 46px;
    padding-right: 54px;
  }
}
@media (max-width: 767px) {
  .container--advantages {
    padding: 0;
  }
}

.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  padding: 0;
  margin: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
}
@media (max-width: 1023px) {
  .header {
    position: absolute;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 0 25px;
  }
}

.header__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1023px) {
  .header__wrapper {
    display: grid;
    grid-template-columns: 215px 1fr;
    align-items: start;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    grid-template-columns: 35px 1fr;
  }
}

.header__wrapper a {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 30px;
  margin-bottom: 35px;
}
@media (max-width: 1023px) {
  .header__wrapper a {
    margin: 0;
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .header__wrapper a {
    padding-top: 15px;
    padding-bottom: 20px;
  }
}
@media (hover: hover) {
  .header__wrapper a:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__wrapper a.focus-visible:focus {
  opacity: 0.8;
}

.header__svg-logo {
  width: 215px;
  height: 55px;
}
@media (max-width: 767px) {
  .header__svg-logo {
    display: none;
  }
}

.header__svg-logo--mobile {
  display: none;
}
@media (max-width: 767px) {
  .header__svg-logo--mobile {
    display: block;
    width: 35px;
    height: 35px;
  }
}

.header__contacts {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 480px;
  margin-left: 15px;
  margin-top: 40px;
  margin-bottom: 50px;
}
@media (max-width: 1023px) {
  .header__contacts {
    gap: 20px;
    width: 100%;
    margin-left: 0;
    padding-right: 20px;
    margin-top: 20px;
    margin-bottom: 40px;
    justify-content: end;
  }
}
@media (max-width: 767px) {
  .header__contacts {
    padding-right: 10px;
    margin-top: 10px;
    margin-bottom: 20px;
  }
}

.header__contacts a {
  margin-top: 5px;
  margin-bottom: 5px;
}
@media (max-width: 1023px) {
  .header__contacts a {
    padding: 10px;
    margin-top: 0;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .header__contacts a {
    padding: 10px;
  }
}
@media (hover: hover) {
  .header__contacts a:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.header__contacts a.focus-visible:focus {
  opacity: 0.8;
}

.header__contacts span {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f6;
  min-width: 205px;
  padding-left: 15px;
}
@media (max-width: 1023px) {
  .header__contacts span {
    display: none;
  }
}

.header__button {
  background-color: #041827;
  color: #f2f5f6;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
  padding: 10px 15px;
  margin-top: 35px;
  margin-bottom: 45px;
  margin-right: 63px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  flex: 1;
  max-width: 160px;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.button {
  display: inline-flex;
  box-sizing: border-box;
