  background-color: #041827;
  justify-content: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid #00b2ff;
  border-radius: 3px;
  margin-right: 0;
}
@media (max-width: 1023px) {
  .callback__form-button {
    margin-right: 55px;
  }
}
@media (max-width: 767px) {
  .callback__form-button {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  overflow-y: auto;
  max-height: 100vh;
  display: block;
  background: rgba(3, 16, 27, 0.7);
}

.modal--closed {
  visibility: hidden;
}

.modal--opened {
  visibility: visible;
  position: fixed;
  right: 0;
  left: 0;
}

.modal__overlay {
  flex-shrink: 0;
  flex-grow: 0;
  width: 100%;
  min-height: 100%;
  margin: auto;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
}

.modal__window {
  position: relative;
  top: 0;
  right: 8px;
  width: 500px;
  max-width: 100%;
  padding-top: 35px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 45px;
  border-radius: 3px;
  margin-top: 20px;
  margin-bottom: 55px;
  transition: transform 0.15s ease 0s, opacity 0.15s ease 0s;
  transform: scale(1);
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .modal__window {
    top: 0;
    left: 0;
    right: 0;
  }
}

.modal__close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 20px;
  height: 20px;
  padding: 20px;
  background-color: #00b2ff;
  border: none;
}

.modal__close-button::before {
  position: absolute;
  content: "";
  top: 21px;
  right: 7px;
  width: 26px;
  height: 1px;
  background-color: #ffffff;
  transform: rotate(45deg);
}

.modal__close-button::after {
  position: absolute;
  content: "";
  top: 21px;
  right: 7px;
  width: 26px;
  height: 1px;
  background-color: #ffffff;
  transform: rotate(-45deg);
}

.modal__window h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 43px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
  margin-bottom: 4px;
}

.modal__window p {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin: 0;
  margin-right: 40px;
  margin-bottom: 40px;
}

.modal__form {
  display: flex;
  flex-wrap: wrap;
}

.modal__form input {
  width: 100%;
  border: none;
  overflow: auto;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  padding: 14px;
  margin-bottom: 20px;
  color: #ffffff;
}
@media (hover: hover) {
  .modal__form input:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}

.modal__form-block {
  width: 100%;
  position: relative;
}
.modal__form-block .input-message {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 10;
}
.modal__form-block .input-message.is-invalid {
  color: rgb(153, 24, 24);
}
.modal__form-block .input-message.is-valid {
  color: rgb(24, 124, 13);
}

.modal__form textarea {
  width: 100%;
  height: 155px;
  border: none;
  resize: none;
  overflow: auto;
  padding: 25px 15px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  background-color: rgba(4, 20, 39, 0.15);
  color: #ffffff;
  border-radius: 3px;
  margin-bottom: 20px;
}
@media (hover: hover) {
  .modal__form textarea:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}

.modal__form textarea::placeholder,
.modal__form-block input::placeholder {
  color: #ffffff;
}

.modal__form-agreement {
  position: relative;
  display: flex;
  order: 1;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 20px;
  padding-left: 9px;
}
.modal__form-agreement .input-message {
  position: absolute;
  left: 10px;
  bottom: -25px;
  z-index: 10;
}
.modal__form-agreement .input-message.is-invalid {
  color: rgb(153, 24, 24);
}
.modal__form-agreement .input-message.is-valid {
  color: rgb(24, 124, 13);
}

.modal__form-agreement label {
  display: flex;
  cursor: pointer;
}

.modal__form-checkbox {
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  flex-shrink: 0;
}

.modal__form-input[type=checkbox] {
  width: fit-content;
}

.modal__form-svg {
  display: none;
}

input[type=checkbox]:checked ~ .modal__form-checkbox {
  display: none;
}

input[type=checkbox]:checked ~ .modal__form-svg {
  display: block;
  flex-shrink: 0;
}

input[type=checkbox]:focus ~ .modal__form-svg,
input[type=checkbox]:focus ~ .modal__form-checkbox {
  outline: 2px solid #000000;
  outline-offset: 2px;
}

.modal__agreement-text {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  margin-left: 11px;
}
@media (hover: hover) {
  .modal__agreement-text:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.modal__agreement-text.focus-visible:focus {
  opacity: 0.8;
}

.modal__form-button {
  padding: 12px 25px;
  width: 100%;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 700;
  color: #ffffff;
  background-color: #041827;
  justify-content: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border: 1px solid #00b2ff;
  border-radius: 3px;
}

